import { Navigator, resultsPageService, UserSwipe, useRx } from 'common';
import { Text } from 'components';
import React from 'react';

type TResultSwitcherProps = {
  onClear: VoidFunction | undefined;
};

export function ResultsMenuSheet({ onClear }: TResultSwitcherProps): JSX.Element {
  const view = useRx(resultsPageService.state$);
  const { selectedCount, rejectedCount } = resultsPageService.getResultsCount();

  return (
    <div>
      <Text
        align="center"
        onClick={Navigator.navigateToLikes}
        size="l"
        color={view === UserSwipe.SELECT ? 'red' : 'white'}
      >
        your likes {selectedCount}
      </Text>
      <Text
        align="center"
        onClick={Navigator.navigateToSecretDesires}
        size="l"
        color={view === UserSwipe.REJECT ? 'red' : 'white'}
      >
        your secret desires {rejectedCount}
      </Text>
      {onClear && <Text
        size="l"
        onClick={onClear}
        align="center"
      >
        clear all
      </Text>}
    </div>
  );
}
